export const theme = {
  cBlue: '#007bec',
  padding: '50px 10px',
  setPadding: (padding) => `padding: ${padding}px`,
  setMediaQuery: (size) => {
    const breakpoints = {
      // XL
      1: {
        deviceSize: '1024',
        max: {
          size: '20px;',
          height: '40px;'
        },
        min: {
          size: '40px;',
          height: '60px;'
        }
      },
      // L
      2: {
        deviceSize: '1024',
        max: {
          size: '18px;',
          height: '30px;'
        },
        min: {
          size: '30px;',
          height: '40px;'
        }
      },
      // M
      3: {
        deviceSize: '1024',
        max: {
          size: '14px;',
          height: '25px;'
        },
        min: {
          size: '25px;',
          height: '40px;'
        }
      },
      // S
      4: {
        deviceSize: '1024',
        max: {
          size: '13px;',
          height: '25px;'
        },
        min: {
          size: '20px;',
          height: '30px;'
        }
      },
      // XS
      5: {
        deviceSize: '1024',
        max: {
          size: '11px;',
          height: '10px'
        },
        min: {
          size: '18px;',
          height: '20px'
        }
      },
      6: {
        deviceSize: '1024',
        max: {
          size: '8px;'
        },
        min: {
          size: '15px;'
        }
      }
    };

    const limitation = '1024';
    const minSize = breakpoints[size].min.size;
    const maxSize = breakpoints[size].max.size;
    const minLineHeight = breakpoints[size].min.height;
    const maxLineHeight = breakpoints[size].max.height;

    return `@media (min-width: ${limitation}px) { font-size: ${minSize}px; line-height: ${minLineHeight}; } @media (max-width: ${limitation}px) { font-size: ${maxSize}; line-height: ${maxLineHeight};  }`;
  }
};
